@import './mixins.scss';
@import './variables.scss';

* {
  box-sizing: border-box;
}
body,
html {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
:root {
  --baseD0: 0.5rem;
  --baseD1: 1rem;
  --baseD2: 2rem;
  --basePop: #163d57;
  // --basePop: #125ee1;
  --baseBg: #f9f9fb;
}
.container {
  max-width: 1000px;
  margin: 0 auto;
  // margin-bottom: 3em;
}
.table-name {
  margin-bottom: 1em;
  color: rgb(92, 91, 91);
  font-size: large;
  font-weight: bold;
}
.table {
  display: flex;
  justify-content: center;
  margin: 4em auto;
}
.data-table {
  margin-top: var(--baseD0);
  border-spacing: 0;
  margin-bottom: var(--baseD2);
  vertical-align: top;
  max-width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: pre-wrap;
  display: inline-block;
  border-collapse: collapse;
  border-spacing: 0;
}
.data-table tbody {
  -webkit-overflow-scrolling: touch;
}
.data-table th {
  text-align: left;
  background: var(--basePop);
  color: var(--baseBg);
}
.data-table th[scope='col'],
.data-table td {
  padding: 6px 12px;
}
.data-table th[scope='row'] {
  background: none;
  color: inherit;
}
// .data-table th[scope='col'] {
//   // @include md {
//   //   white-space: nowrap;
//   // }
// }
.data-table td {
  border-bottom: 1px solid var(--basePop);
}

.chart-cont {
  width: 100%;
  min-height: 20vh;
  display: inline-flex;
  flex-direction: column;
}
